@import '../../common/common.scss';

.bookHeader {
    width: 70%;
    position: relative;

    .logo {
        height: 50px;
        object-fit: contain;
        margin: 30px 0;
    }

    .back {
        height: 50px;
        position: absolute;
        top: 20px;
        right: 0;

        &:hover {
            cursor: pointer;
        }
    }

    .title {
        width: 100%;
        padding-left: 20px;
        font-size: 20px;
        font-weight: bold;
    }

    .line {
        width: 100%;
        height: 2px;
        background-color: lightgray;
        margin: 10px 0;
    }

    @include mobile {
        width: 100%;
        height: 20%;

        .back {
            display: none;
        }
    }
}